'use client';

import { useState } from 'react';
import { Bookmark, Share2, Info, Maximize2, X, Sparkles, Brain, Zap, Activity, Lightbulb, Wrench, History } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import clsx from 'clsx';
import { toggleSaveContent } from '@/app/actions';
import { trackCategoryView, trackSkip } from '@/app/actions/tracking';
import { markAsSeen } from '@/app/actions/seen';
import { useRouter } from 'next/navigation';

const categoryConfig: Record<string, { color: string, icon: any }> = {
  'Tech Tips': { color: 'text-accent-blue border-accent-blue bg-accent-blue/10', icon: Zap },
  'Concepts Simplified': { color: 'text-accent-yellow border-accent-yellow bg-accent-yellow/10', icon: Brain },
  'Shortcuts & Tools': { color: 'text-accent-orange border-accent-orange bg-accent-orange/10', icon: Wrench },
  'Health & Focus': { color: 'text-accent-green border-accent-green bg-accent-green/10', icon: Activity },
  'Useful Facts': { color: 'text-accent-blue border-accent-blue bg-accent-blue/10', icon: Sparkles },
  'Daily Fixes': { color: 'text-accent-yellow border-accent-yellow bg-accent-yellow/10', icon: Lightbulb },
  'History Stories': { color: 'text-accent-orange border-accent-orange bg-accent-orange/10', icon: History },
  Default: { color: 'text-gray-500 border-gray-500 bg-gray-500/10', icon: Info }
};

interface ContentCardProps {
  id: string;
  title: string;
  description: string;
  category: string;
  tags?: string[];
  source?: string;
  isSaved?: boolean;
}

export default function ContentCard({ 
  id, 
  title, 
  description, 
  category, 
  tags,
  source, 
  isSaved = false, 
}: ContentCardProps) {
  const [saved, setSaved] = useState(isSaved);
  const [expanded, setExpanded] = useState(false); 
  const router = useRouter();
  
  const config = categoryConfig[category] || categoryConfig.Default;
  const CategoryIcon = config.icon;

  const handleSaveToggle = async (e: React.MouseEvent) => {
    e.stopPropagation();
    const previousState = saved;
    setSaved(!saved); 
    await toggleSaveContent(id, previousState); 
  };

  const handleExpand = () => {
    if (!expanded) {
      setExpanded(true);
      trackCategoryView(category);
      markAsSeen(id); 
    }
  };

  const handleSkip = async (e: React.MouseEvent) => {
    e.stopPropagation();
    await trackSkip(category);
    await markAsSeen(id);
    router.refresh(); // This will trigger the server component to re-render without this card
  };

  // Fun styling for the description: highlights some words randomly (simulated)
  const renderStyledDescription = (text: string) => {
    const words = text.split(' ');
    return words.map((word, i) => {
      // Pick some words to highlight based on some "silly" logic
      const isFun = word.length > 7 || i % 7 === 0;
      const colors = ['text-accent-orange', 'text-accent-green', 'text-accent-yellow', 'text-accent-blue'];
      const color = colors[i % colors.length];
      
      return (
        <span 
          key={i} 
          className={clsx(
            isFun ? `${color} font-black italic tracking-tight` : "text-gray-300 font-medium",
            "inline-block mr-1 transition-all hover:scale-110 cursor-default"
          )}
        >
          {word}
        </span>
      );
    });
  };

  return (
    <>
      <motion.div 
        layoutId={`card-${id}`}
        onClick={handleExpand}
        className={clsx(
            "w-full bg-gray-900/40 border border-gray-800 rounded-2xl overflow-hidden active:scale-[0.98] transition-transform cursor-pointer",
            expanded ? "fixed inset-x-4 top-[10%] bottom-[10%] z-50 bg-[#0a0a0a] shadow-2xl border-gray-700" : "relative"
        )}
      >
        <div className="p-5 flex flex-col h-full">
           <div className="flex items-center justify-between mb-3">
             <div className={clsx("flex items-center gap-1.5 px-2.5 py-1 rounded-full border text-[10px] font-black uppercase tracking-widest", config.color)}>
               <CategoryIcon size={12} />
               {category}
             </div>
             {expanded && (
                <button 
                  onClick={(e) => { e.stopPropagation(); setExpanded(false); }}
                  className="p-1.5 bg-gray-900 rounded-full text-gray-400 hover:text-white"
                >
                  <X size={18} />
                </button>
             )}
           </div>

           <h3 className={clsx(
             "font-bold text-white leading-tight mb-2",
             expanded ? "text-2xl tracking-tighter" : "text-lg line-clamp-2"
           )}>
             {title}
           </h3>

           {expanded ? (
              <div className="flex-1 overflow-y-auto no-scrollbar py-2">
                <div className="text-base leading-relaxed mb-6 whitespace-pre-wrap">
                  {renderStyledDescription(description)}
                </div>
                
                {tags && tags.length > 0 && (
                  <div className="flex flex-wrap gap-2 mb-6">
                    {tags.map(tag => (
                      <span key={tag} className="text-[10px] px-2 py-0.5 bg-gray-800 text-gray-400 rounded-md">
                        #{tag}
                      </span>
                    ))}
                  </div>
                )}

                {source && (
                  <div className="mt-auto pt-4 border-t border-gray-800">
                    <p className="text-[10px] text-gray-500 uppercase font-bold mb-1">Source</p>
                    <p className="text-sm text-gray-400 font-medium italic">"{source}"</p>
                  </div>
                )}
              </div>
           ) : (
              <p className="text-gray-500 text-xs line-clamp-2 mb-4 leading-relaxed font-mono">
                {description}
              </p>
           )}

           <div className={clsx(
             "flex items-center justify-between pt-3 mt-auto",
             expanded ? "border-t border-gray-800" : ""
           )}>
             <div className="flex items-center gap-4">
               <button 
                 onClick={handleSaveToggle}
                 className="flex items-center gap-1.5 text-xs font-bold text-gray-400 active:scale-90 transition-transform"
               >
                 <Bookmark className={clsx("w-4 h-4", saved ? "fill-accent-yellow text-accent-yellow" : "")} />
                 {saved ? "SAVED" : "SAVE"}
               </button>

               {!expanded && (
                 <button 
                   onClick={handleSkip}
                   className="text-[10px] font-bold text-gray-600 hover:text-accent-orange transition-colors"
                 >
                   SKIP
                 </button>
               )}
             </div>
             
             <div className="flex items-center gap-4">
                <button className="text-gray-400 active:scale-90 transition-transform">
                  <Share2 className="w-4 h-4" />
                </button>
                {!expanded && <Maximize2 className="w-4 h-4 text-gray-600" />}
             </div>
           </div>
        </div>
      </motion.div>

      <AnimatePresence>
        {expanded && (
          <motion.div 
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={() => setExpanded(false)}
            className="fixed inset-0 bg-black/80 backdrop-blur-sm z-40"
          />
        )}
      </AnimatePresence>
    </>
  );
}
